import styled from "styled-components";
export const HpBar = styled.div`
    border: 2px solid black;
    background-image: ${(props)=>{if(props.type==="hero"){return "linear-gradient(to bottom, rgba(157,213,58,1) 0%,rgba(161,213,79,1) 50%,rgba(128,194,23,1) 51%,rgba(124,188,10,1) 100%)"}else if(props.type==="enemy"){return "linear-gradient(to bottom, rgba(248,80,50,1) 0%,rgba(241,111,92,1) 50%,rgba(246,41,12,1) 51%,rgba(240,47,23,1) 71%,rgba(231,56,39,1) 100%)"}}};
    background-size: ${(props)=>{return (`${Math.floor((props.cvalue/props.maxvalue)*100)}% 100%;`)}}
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    transition: background-size ease-out 1s 0s;
    font-size: 20px;
    color: white;
    font-family: sans-serif;
    margin-top: 5px;
`